.pdf a::before {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 521px 497px;
}

.pdf a {
  display: flex;
  padding-left: 35px;
  border-bottom: 0;
  color: #999999;
}

.pdf a .pdf__text {
  font-size: 15px;
  line-height: 15px;
  border-bottom: 1px solid #999999;
  display: block;
  margin: 3px 0;
}

.pdf a::before {
  content: '';
  position: absolute;
  top: -2px;
  left: 0px;
  width: 26px;
  height: 26px;
  background-position: -143px -451px;
}

@media only screen and (max-width: 991px) {
  .pdf {
    display: none;
  }
}